//Global App Backbone
app || (app = {});

(function (window, document, undefined) {

    app.PrioridadModel = Backbone.Model.extend({

        urlRoot: function () {
            return window.Misc.urlFull( Route.route('prioridades.index') );
        },
        idAttribute: 'id',
        defaults: {
            'prioridad_nombre': '',
            'prioridad_activo': true
        }
    });

})(this, this.document);
